* {
    padding: 0;
    margin: 0;
    border: none;
    outline: none;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

header,
main,
footer {
    width: 100%;
    height: auto;
    position: relative;
}

.gridContainer {
    min-width: 375px;
    max-width: 1360px;
    margin: 0 auto;
}

a {
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}

button {
    cursor: pointer;
}

/*.wrapper img {
    width: 100%;
}*/
h1 {
    font: 700 normal 3.6rem 'NoirPro';
    line-height: 120%;
    color: #311746;
    margin: 20px;
    width: 50%;
}

h1 b {
    color: #E73348;
}

h2 {
    font: normal 3rem "NoirPro";
    font-size: 30px;
    line-height: 120%;
    color: #ffffff;
}

/*---------------------- header ---------------*/
.header .flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.flex .blockLogo {
    width: 200px;
}

.flex .blockAcount {
    width: 50px;
}

.flex .blockLogo,
.flex .blockAcount {
    margin: 20px;
}

.dropdown {
    position: relative;
    display: inline-block;
    text-align: left;
    width: 50px;
}

.dropdown-text {
    cursor: pointer;
    //position: absolute;
    //text-indent: 10px;
    //line-height: 32px;
    //background-color: #eee;
    //border: 1px solid #ccc;
    //border-radius: 3px;
    //box-shadow: 0 1px 0 rgba(255, 255, 255, .9) inset, 0 1px 3px rgba(0, 0, 0, .1);
    width: 100%;
}

.dropdown-text:after {
    position: absolute;
    right: 6px;
    top: 15px;
    content: '';
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 5px 4px 0 4px;
    border-color: #555 transparent transparent transparent;
}

.dropdown-text,
.dropdown-content a {
    color: #333;
    text-shadow: 0 1px #fff;
}

.dropdown-toggle {
    font-size: 0;
    z-index: 1;
    cursor: pointer;
    position: absolute;
    top: 0;
    border: none;
    padding: 0;
    margin: 0 0 0 1px;
    background: transparent;
    text-indent: -10px;
    height: 34px;
    width: 100%;
}

.dropdown-toggle:focus {
    outline: 0;
}

.dropdown-content {
    -webkit-transition: all .25s ease;
    -moz-transition: all .25s ease;
    -ms-transition: all .25s ease;
    -o-transition: all .25s ease;
    transition: all .25s ease;
    list-style-type: none;
    position: absolute;
    top: 32px;
    padding: 0;
    margin: 0;
    opacity: 0;
    visibility: hidden;
    border-radius: 3px;
    text-indent: 10px;
    line-height: 32px;
    background-color: #eee;
    border: 1px solid #ccc;
    width: 100px;
    right: 0;
}

.dropdown-content a {
    display: block;
}

.dropdown-content a:hover {
    //background: #e8e8e8;
}


.dropdown-toggle:hover~.dropdown-text,
.dropdown-toggle:focus~.dropdown-text {
    // background-color: #e8e8e8;
}

.dropdown-toggle:focus~.dropdown-text {
    //box-shadow: 0 1px 3px rgba(0, 0, 0, .2) inset, 0 1px 0 rgba(255, 255, 255, 0.8);
    z-index: 2;
}

.dropdown-toggle:focus~.dropdown-text:after {
    border-width: 0 4px 5px 4px;
    border-color: transparent transparent #555 transparent;
}

.dropdown-content:hover,
.dropdown-toggle:focus~.dropdown-content {
    opacity: 1;
    visibility: visible;
    top: 42px;
}

/*----------- Featured Creators -----------*/
.flex1 .blockSearch {
    margin: 20px;
    width: 100%;
}

.blockSearch .formSearch {
    background: #FFF7F7;
    border-radius: 4px;
    padding: 15px;
}

.formSearch input[type="search"] {
    background: #FFF7F7;
    width: 90%;
}

.formSearch input[type="submit"] {
    text-indent: -9999px;
    width: 18px;
    height: 18px;
    background: url('../img/search.png') center no-repeat;
    cursor: pointer;
}



.flex1 .textSection {
    display: flex;
    flex-wrap: wrap;
    //justify-content: space-between;
}

.flex1 .slider article {
    width: 256px;
    height: 430px;
    margin: 0 15px;
    position: relative;
}


.flex1 .slider .article:nth-of-type(1) {
    background-color: #E73348;
}

.slick-next,
.slick-prev {
    width: 64px;
    height: 64px;
    border-radius: 50%;
}

.slider .slick-prev {
    background: url('../img/Arrow-left.png') center no-repeat #E73348;
    right: 100px;
    top: -145px;
}

.slider .slick-next {
    background: url('../img/Arrow-right.png') center no-repeat #E73348;
    right: 15px;
    bottom: 513px;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
    opacity: .25;
}


article .imageArticle {
    position: absolute;
}

article .textArticle {
    position: absolute;
    margin: 20px;
    bottom: 0;
}

.textArticle p {
    font: 300 normal 1.8rem 'NoirPro';
    line-height: 136%;
    color: #ffffff;
    margin-top: 10px;
}

.textArticle .linkArticle {
    background: #E73348;
    padding: 21px 45px;
    border-radius: 4px;
    margin-top: 20px;
    text-align: center;
    width: 100%;
    font: normal 1.6rem 'NoirPro';
    line-height: 130%;
    color: #ffffff;
}

.textArticle .linkArticleCreate {
    background: #ffffff;
    padding: 21px 35px;
    border-radius: 4px;
    margin-top: 20px;
    text-align: center;
    width: 100%;
    font: normal 1.6rem 'NoirPro';
    line-height: 130%;
    color: #E73348;
}

.linkArticle .open {
    display: none;
}

/*-------------- media screen 1280px ---------------*/
@media screen and (min-width: 1280px) {
    .flex1 .slider {
        padding-left: 80px;
    }

    h1 {
        font-size: 4.8rem;
        margin: 0 0 40px 95px;
    }

    .flex1 .textSection {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
    }

    .slider .slick-prev {
        background: url('../img/Arrow-left.png') center no-repeat #E73348;
        top: calc(50% - 35px);
        left: 0;
    }

    .slider .slick-next {
        background: url('../img/Arrow-right.png') center no-repeat #E73348;
        bottom: calc(50% - 35px);
        right: 0;
    }

    .slick-next,
    .slick-prev {
        width: 70px;
        height: 70px;
    }

    .flex1 .blockSearch {
        margin: 20px;
        width: auto;
    }

    .formSearch input[type="search"] {
        background: #FFF7F7;
        width: auto;
    }

    .dropdown-content {
        width: 140px;
    }

    .dropdown {
        width: 170px;
        display: flex;
        align-items: center;
    }

    .flex .blockAcount {
        width: auto;
    }

    .dropdown .imgUser {
        margin-right: 10px;
    }

    .dropdown-text span:nth-of-type(1) {
        font: 300 normal 1.4rem 'NoirPro';
        line-height: 136%;
        color: #E73348;
    }

    .dropdown-text span:nth-of-type(2) {
        font: 700 normal 1.6rem 'NoirPro';
        line-height: 130%;
        color: #E73348;
    }
}

