/* Arrows */

.slick-prev,
.slick-next {
    position: absolute;
    display: block;
    /*height: 20px;
    width: 20px;*/
    line-height: 0px;
    font-size: 0px;
    cursor: pointer;
    /*background:#000000;*/
    color: transparent;
    /*top: 50%;*/
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    padding: 0;
    border: none;
    outline: none;

    &:hover,
    &:focus {
        outline: none;
        //background: red;
        color: transparent;

        &:before {
            opacity: $slick-opacity-on-hover;
        }
    }

    &.slick-disabled:before {
        opacity: $slick-opacity-not-active;
    }

    &:before {
        font-family: $slick-font-family;
        font-size: 20px;
        line-height: 1;
        color: $slick-arrow-color;
        opacity: $slick-opacity-default;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
    }
}

.slick-prev {
    z-index: 1;
    //left: 84px;

    [dir="rtl"] & {
        left: auto;
        right: -25px;
    }

    &:before {
        content: ''
            /*$slick-prev-character*/
        ;

        [dir="rtl"] & {
            content: ''
                /*$slick-next-character*/
            ;
        }
    }
}

.slick-next {
    z-index: 1;
    right: 84px;

    [dir="rtl"] & {
        left: -25px;
        right: auto;
    }

    &:before {
        content: ''
            /*$slick-next-character*/
        ;

        [dir="rtl"] & {
            content: ''
                /*$slick-prev-character*/
            ;
        }
    }
}



.slick-dots {
    position: absolute;
    //bottom: -50px;
    bottom: 0;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    margin: 0;
    width: 100%;

    /*left:calc(50% - 120px);*/
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 10px;
        padding: 0;
        cursor: pointer;

        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;

            &:hover,
            &:focus {
                outline: none;

                &:before {
                    /*opacity: $slick-opacity-on-hover;*/
                }
            }

            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: ''
                    /*$slick-dot-character*/
                ;
                width: 20px;
                height: 20px;
                border: .1rem solid #F4A876;
                border-radius: 50%;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
            }
        }

        &.slick-active button:before {
            background: #F4A876;
        }
    }
}